import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import CategoryLinksStyle from './category-left-menu'

let CategoryLinks = [
    {
        linkslabel:"Accessories",
    },
    {
        linkslabel:"Bags",
    },
    {
        linkslabel:"Clothing",
    },
    {
        linkslabel:"Shoes",
    }
]

const CategoryLinksBlock = ( CategoryLinks&& Array.isArray(CategoryLinks) && CategoryLinks).map(item => (
        <Col md="3" sm="3">
            <div className="category-links-block">
                <div className="product-name">{item.productname}</div>
            </div>
        </Col>

    )) ||
    "Loading...";
    const MenProductBlock = (MenProductListing &&
        Array.isArray(MenProductListing)
        && MenProductListing).map(item => (
            <Col md="3" sm="3">
                <div className="product-content-block">
                    <img src={item.image} alt={item.image} />
                    <div className="product-name">{item.productname}</div>
                    <div className="product-discription">{item.desc}</div>
                    <div className="price">{item.price}</div>
                </div>
            </Col>
    
        )) ||
        "Loading...";




    export default (props) => {
        return (
            <>
                <div className="left-link">
                   {CategoryLinksBlock}
                </div>
            </>
        )
    }